<template lang="html">
  <canvas ref="upfitter" class="smallupfitter"></canvas>
</template>

<script>
// 
// var WIDTH_legend = 300;
// var HEIGHT_legend = 60;

export default {
  name: 'SmallUpfitter',
  props: [
    'params',
    'sizeInPixel',
    'legendRatio', // 
    'scaleRatio', // 
    'compassRatio', // 
  ],
  data() {
    return {
      ctxt: null, // 
      // 
      LTWH: {
        legend: [], // 
        scale: [], // 
        compass: [], // 
      },
      cm2px: null,
      /**
       * 
       */
      params_cache: null,
      // 
      compassImage: null,
      active: true, // canvas

      // 
      WIDTH_legend: 220,
      HEIGHT_legend: 50,

      // 
      HEIGHT_scale: 50,

    }
  },
  methods: {
    /**
     * 
     * @param Object nv 
     * @param Number index 
     */
    cacheUsable( nv, index ) {
      var cache = this.params_cache[ index ];
      for( var attr in cache ) {
        if( cache.hasOwnProperty( attr ) ) {
          // ,;
          if( ( attr === 'image' && !cache[ attr ] ) || ( nv.hasOwnProperty( attr ) && cache[ attr ] !== nv[ attr ] ) ) {
            return false;
          } else if( attr === 'image' ) continue;
        }
      }
      return true;
    },
    /**
     * 
     * @param [number] x x
     * @param [number] y y
     * @param [number] w 
     * @param [number] h 
     * @param [string] color 
     * @return [object] imageData 
     */
    calcImage( x, y, w, h, color ) {
      // rgb
      var sColor = color.toLowerCase();
      var colorArr = [];
      // 
      if(sColor && /^#([0-9a-fA-f]{3}|[0-9a-fA-f]{6})$/.test(sColor)){
        // #fff
        if(sColor.length === 4){
          var sColorNew = "#";
          for( var i = 1; i < 4; i++ ){
            sColorNew += sColor.slice( i,i + 1 ).concat( sColor.slice( i, i + 1 ) );
          }
          sColor = sColorNew;
        }
        //
        for(var i=1; i<7; i+=2){
          colorArr.push(parseInt("0x"+sColor.slice( i,i+2 ) ) );
        }
      }else{
        // rgba 
        color.replace( /[rR][gG][bB][aA]?\([\s]*(\d{1,3}),[\s]*(\d{1,3}),[\s]*(\d{1,3}),?[\s]*(0\.\d{1,2}|1|0)?\){1}/g, function( match, $1, $2, $3, $4 ){
          colorArr = [ +$1, +$2, +$3 ];
        } )
      }

      // 
      var imageData = this.ctxt.getImageData( x, y, w, h );
      var data = imageData.data;
      var r = colorArr[0] / 255.0;
      var g = colorArr[1] / 255.0;
      var b = colorArr[2] / 255.0;

      // 
      for (var i = 0, ii = data.length; i < ii; i += 4) {
        if( data[ i + 3 ] == 0 ) {
          // 
          data[ i ] = 100;
          data[ i + 1 ] = 100;
          data[ i + 2 ] = 100;
          data[ i + 3 ] = 123;
        } else {
          data[ i ] *= r;
          data[ i + 1 ] *= g;
          data[ i + 2 ] *= b;
        }

      }

      return imageData;

    },

    /**
     * 
     * @param  {[type]} ev  []
     * @param  {[type]} dx [xx]
     * @param  {[type]} dy [yy]
     */
    updatecompass( ev, dx, dy ) {
      if( !ev ) {
        let a = this.LTWH.compass;
        // 
        this.active || this.ctxt.clearRect( ...a );
        // 
        if( this.compassImage ) {
          this.ctxt.drawImage( this.compassImage, a[0], a[1] );
        } else {
            let image = new Image();
            let scope = this;
            image.src = './static/compass.png';
            image.onload = function(){
                scope.ctxt.drawImage( this, a[0], a[1] );
                scope.compassImage = this;
            }
        }

        return
      }

      // 
      this.updateLTWH( 'compass', ev, dx, dy );
    },

    /**
     * [updatelegend ]
     * @param  {[type]} ev  []
     * @param  {[type]} dx [xx]
     * @param  {[type]} dy [yy]
     * @return {[type]}    [description]
     */
    updatelegend( ev, dx, dy ) {
      /*
       
       
       */
      if( !ev ) {
        // 
        // 
        var needUpdate = this.needUpdate || !this.params.every( this.cacheUsable );
        // 
        this.active || this.ctxt.clearRect( ...this.LTWH.legend );

        let a = this.LTWH.legend;
        let ctxt = this.ctxt;
        let scope = this;

        const d = 0;

        // list
        // listpromisepromise resolve
        // 
        let list = this.params.map( function( param, index ){
          return new Promise( ( resolve, reject ) => {
            // 
            if( needUpdate ) {
              // 
              if( /\.png/i.test( param.stylePic ) ) {
                /*
                
                ol.style.IconImagereplaceColor
                 */
                var imgObj = new Image();
                imgObj.src = param.stylePic;

                //canvas
                imgObj.onload = function() {
                  let x = a[0] + d; // 
                  let y = a[1] + scope.HEIGHT_legend * index + d;
                  let s =  scope.HEIGHT_legend - d * 2; // 
                  ctxt.drawImage(this, x, y, s, s);//thisimgObj,HEIGHT_legend - d * 2 
                  let imageData = scope.calcImage( x, y, s, s, param.color );
                  // 
                  scope.params_cache[index][ 'image' ] = imageData;
                  resolve( [ index, 'putImageData', imageData, x, y ] );
                  // ctxt.putImageData( imageData, x, y );

                }
              }
              // 
              else {
                scope.params_cache[index][ 'image' ] = param.stylePic;
                resolve( [ index, 'fillRect', param.stylePic, a[0] + d, a[1] + scope.HEIGHT_legend * index + d, scope.HEIGHT_legend - d * 2, scope.HEIGHT_legend - d * 2 ] )
                // ctxt.fillRect( a[0] + d, a[1] + scope.HEIGHT_legend * index + d, scope.HEIGHT_legend - d * 2, scope.HEIGHT_legend - d * 2 );
              }

              // 
              for( var attr in scope.params_cache[index] ) {
                if(attr === 'image') {
                  continue;
                } else {
                  scope.params_cache[index][ attr ] = param[ attr ];
                }
              }

            } else {
              // 
              if( /\.png/i.test( param.stylePic ) ) {
                resolve( [ index, 'putImageData', scope.params_cache[index][ 'image' ], a[0] + d, a[1] + scope.HEIGHT_legend * index + d ] )
                // ctxt.putImageData( scope.params_cache[index][ 'image' ], a[0] + d, a[1] + scope.HEIGHT_legend * index + d );
              }
              // 
              else {
                scope.params_cache[index][ 'image' ] = param.stylePic;
                resolve( [ index, 'fillRect', param.stylePic, a[0] + d, a[1] + scope.HEIGHT_legend * index + d, scope.HEIGHT_legend - d * 2, scope.HEIGHT_legend - d * 2] )
                // ctxt.fillRect( a[0] + d, a[1] + scope.HEIGHT_legend * index + d, scope.HEIGHT_legend - d * 2, scope.HEIGHT_legend - d * 2 );
              }

            }

            // // 
            // ctxt.font="bold 20px Arial";
            // ctxt.fillStyle="#058";
            // ctxt.fillText( param.name, a[0] + d * 2 + HEIGHT_legend, a[1] + HEIGHT_legend * index + d + HEIGHT_legend / 2 );
          } )

        } )

        // thenpromiseresolve
        Promise.all( list ).then( ( o ) => {
          // 
          ctxt.fillStyle = "rgba(100, 100, 100, 0.5)";
          ctxt.fillRect ( ...scope.LTWH.legend );
          o.forEach( ( value ) => {
            // 
            if( value[ 1 ] === 'fillRect' ) {
              ctxt.fillStyle = value[ 2 ];
              ctxt[ value[1] ]( ...value.slice(3) );
            } else {
              ctxt[ value[1] ]( ...value.slice(2) );
            }

            // 
            ctxt.fillStyle = "#fff";
            ctxt.font = `bold ${ scope.HEIGHT_legend / 3 }px Arial`;
            ctxt.fillText( scope.params[ value[0] ].name, a[0] + d * 2 + scope.HEIGHT_legend, a[1] + scope.HEIGHT_legend * value[0] + d + scope.HEIGHT_legend / 2 );
          } )
        } )

        return
      }

      // 
      this.updateLTWH( 'legend', ev, dx, dy );
    },

    /**
     * 
     * @param  {[number]} scale []
     * @return {[array]}       []
     */
    calcScale( scale ) {
      let s = parseInt( scale ).toString().length - 1;

      // 10*20*50*
      let m;
      let min;
      // 
      let b = [ 1, 2, 5 ];
      b.forEach( function( value ) {
        let t = Math.abs( scale - value * Math.pow( 10, s ) );
        if( !min || t < min ) {
          min = t;
          m = value * Math.pow( 10, s )
        }
      } )

      return [ m / scale, m ];
    },

    /**
     * [updatescale ]
     * @param  {[type]} ev  []
     * @param  {[type]} dx [xx]
     * @param  {[type]} dy [yy]
     * @return {[type]}    [description]
     */
    updatescale( ev, dx, dy ) {
      if( !ev ) {
        let a = this.LTWH.scale;
        // 
        this.active || this.ctxt.clearRect( ...a );

        // map units per pixels
        // 
        let scale = this.__global__.olMap.getView().getResolution() / 2.3794610058302803e-9;
        // [ ]
        let s = this.calcScale( scale );

        // 5 
        let c = 5;
        let x1 = a[0] + 3;
        let x2 = a[0] + 3 + s[0] * this.cm2px * c;
        let y1 = a[1] + a[3] * 0.75 - 7;
        let y2 = a[1] + a[3] * 0.75;

        // 
        // 
        a[ 2 ] = x2 - x1 + 10;

        // this.ctxt.fillStyle = "rgba(200, 100, 0, 0.5)";
        // this.ctxt.fillRect ( ...a );

        // 
        this.ctxt.font = `bold ${ this.HEIGHT_scale / 3 } Arial`;
        this.ctxt.fillStyle="#058";
        this.ctxt.fillText( s[1] * c / 100 + "m", a[0] + 8, a[1] + a[3] / 2 );

        this.ctxt.beginPath();

        this.ctxt.moveTo( x1, y1 );
        this.ctxt.lineTo( x1, y2 );
        this.ctxt.lineTo( x2, y2 );
        this.ctxt.lineTo( x2, y1 );

        // 
        this.ctxt.lineWidth = 5;
        this.ctxt.strokeStyle = "black";
        this.ctxt.stroke();

        return
      }

      // 
      this.updateLTWH( 'scale', ev, dx, dy );
    },
    /**
     * 
     * @param string ele 
     * @param object ev 
     * @param  {[type]} dx [xx]
     * @param  {[type]} dy [yy]
     */
    updateLTWH: function( ele, ev, dx, dy ) {
      let e = ev || event;
      // canvas
      // 
      const X = this.$refs.upfitter.offsetParent.offsetLeft;
      const Y = this.$refs.upfitter.offsetParent.offsetTop;
      let x = e.clientX - X;
      let y = e.clientY - Y;

      // 
      this.ctxt.clearRect( 0, 0, ...this.sizeInPixel );

      // 
      this.LTWH[ ele ][0] = x - dx;
      this.LTWH[ ele ][1] = y - dy;

      for ( var variable in this.LTWH ) {
        if (this.LTWH.hasOwnProperty(variable)) {
          this[ `update${variable}` ]();
        }
      }
    },
    /**
     * 
     * @return {[type]} [description]
     */
    calcLTWH: function() {

      /**
       * []
       * @param  {[boolean]} element []
       * @return {[type]}         [description]
       */
      return function( reset ) {

        // 
        const COUNT = this.params.length;
        let a = this.LTWH[ 'legend' ];
        this.WIDTH_legend = this.sizeInPixel[ 0 ] * this.legendRatio;
        this.HEIGHT_legend = this.WIDTH_legend / 5;
        // x
        a[ 0 ] = reset? this.sizeInPixel[ 0 ] - this.WIDTH_legend - 15 : a[ 0 ];
        // y
        a[ 1 ] = reset? this.sizeInPixel[ 1 ] - this.HEIGHT_legend * COUNT - 15 : a[ 1 ];
        // width
        a[ 2 ] = this.WIDTH_legend;
        // height
        a[ 3 ] = this.HEIGHT_legend * COUNT;

        // 
        this.HEIGHT_scale = this.sizeInPixel[ 1 ] * this.scaleRatio;
        a = this.LTWH[ 'scale' ];
        // x
        a[ 0 ] = 15;
        // y
        a[ 1 ] = this.sizeInPixel[ 1 ] - this.HEIGHT_scale - 10;
        // width
        a[ 2 ] = 300;
        // height
        a[ 3 ] = this.HEIGHT_scale;

        // 
        a = this.LTWH[ 'compass' ];
        // x
        a[ 0 ] = 25;
        // y
        a[ 1 ] = 25;
        // width
        a[ 2 ] = 150;
        // height
        a[ 3 ] = 150;
      }
    }(),
    /**
     * 
     * @type {Object}
     * @param {boolean} reset 
     */
    r( reset ) {
      // 
      this.ctxt.clearRect( 0, 0, ...this.sizeInPixel );
      // 
      this.calcLTWH( reset );
      // 
      this.needUpdate = true;
      // 
      for ( var variable in this.LTWH ) {
        if (this.LTWH.hasOwnProperty(variable)) {
          this[ `update${variable}` ]();
        }
      }
      this.needUpdate = false;
    },
  },
  watch: {
    params: {
      /**
       * params
       * @return {[type]} [description]
       */
      handler: function( params ) {
        this.updatelegend();
      },
      deep: true,
    },
    /**
     * 
     * @param  {[type]} sizeInPixel [description]
     * @return {[type]}             [description]
     */
    sizeInPixel: function( nv ) {
      // upfittercanvas
      this.$refs.upfitter.width = nv[ 0 ];
      this.$refs.upfitter.height = nv[ 1 ];
      // 
      // 
      this.r( true );
    },
    /**
     * 
     * @type {[type]}
     */
    legendRatio: function( nv ) {
      this.r( false );
    },
    /**
     * 
     * @type {[type]}
     */
    scaleRatio: function( nv ) {
      this.r( false );
    },
    /**
     * 
     * @type {[type]}
     */
    compassRatio: function( nv ) {
      this.r( false );
    },
  },
  created() {
    // 
    this.params_cache = this.params.map( function( value, index ) {
        return {
            // stylePic
            stylePic: value.stylePic,
            color: value.color,
            // 
            image: null,
        }
    } )
  },
  mounted() {
    // canvasolcanvas
    // this.$refs.upfitter.style[ 'pointer-events' ] = 'none';
    this.ctxt = this.$refs.upfitter.getContext( "2d" );

    // 
    // upfittercanvas
    this.$refs.upfitter.width = this.sizeInPixel[ 0 ];
    this.$refs.upfitter.height = this.sizeInPixel[ 1 ];

    // 
    this.calcLTWH( true );

    /*
    
    csscm/pt/in/pc/mm
    px
     */
    if( !window.cm2px ) {
      window.cm2px = this.cm2px = function() {
        let d = document.createElement( 'div' );
        d.style.width = "1cm";
        document.getElementsByTagName('body')[0].appendChild( d );
        let w = d.offsetWidth;
        document.getElementsByTagName('body')[0].removeChild( d );
        // 1cm = w px
        return w;
      }()
    }

    // 
    for (var variable in this.LTWH) {
      if (this.LTWH.hasOwnProperty(variable)) {
        this[ `update${variable}` ]();
      }
    }

    // 
    this.$refs.upfitter.onmousedown = function( ev ) {
      // yheader
      // 
      const X = scope.$refs.upfitter.offsetParent.offsetLeft;
      const Y = scope.$refs.upfitter.offsetParent.offsetTop;
      var e = ev || event;
      var x = e.clientX - X;
      var y = e.clientY - Y;
      // 
      for ( var variable in scope.LTWH ) {
        if ( scope.LTWH.hasOwnProperty(variable) ) {
          let a = scope.LTWH[ variable ]
          // isPointInPath
          if( ( x >= a[0] && x <= ( a[0] + a[2] ) ) && ( y >= a[1] && y <= ( a[1] + a[3] ) ) ) {
            // 
            let dx = x - a[0];
            let dy = y - a[1];
            scope.$refs.upfitter.onmousemove = ( e ) => {
              scope[ `update${variable}` ]( e, dx, dy );
            }
            // 
            scope.$refs.upfitter.onmouseup = function() {
              this.onmousemove = null;
              this.onmouseup = null;
            }
            return;
          }
        }
      }
    }

    /**
     * 
     * @return {[type]} [description]
     */
    let map = this.__global__.olMap;
    let scope = this;
    map.getView().on('change:resolution',function() {
      scope.updatescale();
    });
  }
}
</script>

<style lang="css">
  .smallupfitter {
    position: absolute;
    top: 0;
    left: 0;
  }
</style>



