/**
 * vue__ob__ob__myob__
 * ()
      
   /() 
 */

import Dep from './dep'

/**
 * Observer
 * @param  {Object} value 
 * @param  {Dep} dep depdep
 * @return {[type]}       [description]
 */
export default function observe( value, dep ) {
  // typeof null object
  // 
  if ( !(value !== null && typeof value === 'object') ) {
    return
  }
  let ob;
  if ( value.hasOwnProperty( '__myob__' ) && value.__myob__ instanceof Observer) {
    ob = value.__myob__
  }
  else {
    ob = new Observer( value, dep );
  }

  return ob
}

/**
 * setget
 * openlayers
 * @type {[type]}
 */

class Observer {
  value = null;
  dep = null; // 

  /**
   * @param {Object} value 
   * @param  {Dep} dep depdep
   */
  constructor ( value, dep ) {
    this.value = value;
    this.dep = dep || new Dep();
    // Observer ( "()"_myob_,Observer )
    Object.defineProperty(value, '__myob__', {
      value: this,
      enumerable: false,
      writable: true,
      configurable: true
    })
    if (Array.isArray(value)) {
      // this.observeArray(value)
    } else {
      this.walk(value, this.dep)
    }
  }

  /**
   * 
   * setget
   */
  walk ( obj, dep ) {
    const keys = Object.keys(obj)
    for (let i = 0; i < keys.length; i++) {
      defineReactive(obj, keys[i], obj[keys[i]], dep)
    }
  }

  /**
   * Observe a list of Array items.
   */
  observeArray ( items ) {
    for (let i = 0, l = items.length; i < l; i++) {
      observe(items[i])
    }
  }
}

/**
 * setget
 * @param  {Object} obj [description]
 * @param  {String} key 
 * @param  {Any} val 
 * @param  {Dep} dep 
 * @return {[type]}     [description]
 */
function defineReactive ( obj, key, val, dep ) {

  // const dep = new Dep();

  const property = Object.getOwnPropertyDescriptor(obj, key)
  if (property && property.configurable === false) {
    return
  }

  // 
  const getter = property && property.get;
  const setter = property && property.set;

  // 
  // let childOb = observe(val, dep); // Observer
  Object.defineProperty(obj, key, {
    enumerable: true,
    configurable: true,
    get: function() {
      const value = getter ? getter.call(obj) : val;
      return value;
    },
    set: function (newVal) {
      // debugger
      const value = getter ? getter.call(obj) : val;

      if ( newVal === value ) {
        return
      }

      if (setter) {
        setter.call(obj, newVal);
      } else {
        val = newVal;
      }
      // childOb = observe(newVal, dep);// 
      // 
      dep.notify();
    }
  })
}



