import Vue from 'vue'
import Router from 'vue-router'
import Mapping from '../components/Mapping'
import Print from '../components/Print'
import Home from '@/components/Home'  //'@': resolve('src'),src'@'
import Data from '../components/Data'

//  Vue.use() 
Vue.use(Router)

// redirect: '/b' ( /aURL /b /b)
// alias: '/b' (/b URL/b/a/a )
// *Routerroutesrouter-view()
export default new Router({
  mode: 'history', //history
  base:'/watermappingzj', //build,Tomcatbaseconfig/index.jsbuild assetsPublicPath: '/zj-water-thematicMapping/',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/data',
      name: 'Data',
      component: Data
    },
    {
      path: '/map',
      name: 'Mapping',
      component: Mapping
    },
    {
      path: '/print',
      name: 'Print',
      component: Print,
    }
  ]
})



