<template>
  <layout :style="{height: '100%', width: '100%'}">
    <layout v-show="showType === 'mapping'" :style="{height: '100%', width: '100%'}">
        <MapMenu :params = 'params'/>
        <MapContainer :size='size' :resolution='resolution' :center='center'>
          <!-- WMTS(MapContainer) -->
          <TileLayer/>
          <!-- (MapContainer) -->
          <template v-for="(param, index) in params">
            <component v-bind:is='param.layer' :param = 'param' :index = 'index' :key = 'index' @feaure-loaded='passFeatures'></component>
          </template>
        </MapContainer>
        <!-- olcanvasUpfitterlabelolcanvas ( kebab-case )-->
        <Label  :params = 'params' :features = 'features' :editable='editable' @pass-context='bindLabelContext'/>  
        <Upfitter :params = 'params' :editable='editable'/>
        <Tool @switch='editMapElements' @draw='draw' :editable='editable' :draw='drawType' :params='params'/>
        <!-- <button @click="printTest"></button> -->
        <!-- <MapLegend :params = 'params' /> -->
    </layout>
    <Print
      v-if="showType === 'print'"
      @toPrintRize='toPrintRize'
      @backToMapping='backToMapping'
      :params = 'params'
      :labelContext='labelContext'
    />
  </layout>
</template>

<script>

  // 
  import MapMenu from './MapMenu.vue'
  import MapContainer from './MapContainer.vue'
  import TileLayer from './TileLayer.vue'
  import LineLayer from './LineLayer.vue'
  import PointLayer from './PointLayer.vue'
  import PolygonLayer from './PolygonLayer.vue'
  import Print from './Print.vue'
  // import MapLegend from './Legend.vue'
  import Tool from './Tool.vue'
  import Upfitter from './Upfitter.vue'
  import Label from './Label.vue'
  import { PolylineParam, PointParam, PolygonParam } from '../core/mappingParams.js'
  import observe from '../core/observer.js'

  /****************************/
  var source = new ol.source.Vector();

  var styleFunction = function(feature) {
    var geometry = feature.getGeometry();
    var styles = [
      // linestring
      new ol.style.Style({
        stroke: new ol.style.Stroke({
          color: '#ffcc33',
          width: 2
        })
      })
    ];

    geometry.forEachSegment(function(start, end) {
      var dx = end[0] - start[0];
      var dy = end[1] - start[1];
      var rotation = Math.atan2(dy, dx);
      // arrows
      styles.push(new ol.style.Style({
        geometry: new ol.geom.Point(end),
        image: new ol.style.Icon({
          src: 'https://openlayers.org/en/v4.6.5/examples/data/arrow.png',
          anchor: [0.75, 0.5],
          rotateWithView: true,
          rotation: -rotation
        })
      }));
    });

    return styles;
  };
  var vector = new ol.layer.Vector({
    source: source,
    style: styleFunction
  });

  var added = false;
  /****************************/

  const mappingParams = {
    // 
    esriGeometryPoint: PointParam,
    // 
    esriGeometryPolyline: PolylineParam,
    // 
    esriGeometryPolygon: PolygonParam,
  }

  /**
   * paramsallDone
   * @type {[type]}
   */
  Object.defineProperty( Array.prototype, 'doneRequestParams', {
    set: function(newValue) {
      if( newValue < 0 || newValue > this.length ) return;
      if( newValue === this.length ) this.allDone();
      this._doneRequestParams = newValue;
    },
    get: function() {
      return this._doneRequestParams || 0;
    }
  })
  // allDone
  Array.prototype.callback = function( fn ) { this.allDone = fn; }

  export default {
    name: 'Mapping',
    components: {
      MapMenu,
      MapContainer,
      TileLayer,
      Label,
      Upfitter,
      Tool,
      LineLayer,
      PointLayer,
      PolygonLayer,
      Print,
      // MapLegend,
    },
    data () {
      return {
        params: null,
        editable: 'feature', // canvas
        drawType: null, // null
        features: [],
        painting: null, // ol.interaction
        showType: 'mapping',
        labelContext: null,

        // 
        size: null,
        // 
        resolution: null,
        // 
        center: null,
      }
    },
    methods: {
      /**
       * canvas
       * @param  {[type]} ctxt [description]
       * @return {[type]}      [description]
       */
      bindLabelContext: function( ctxt ) {
        this.labelContext = ctxt;
      },
      /**
       * 
       * @return {[type]} [description]
       */
      toPrintRize( size ){
        this.size = size;
      },
      /**
       * 
       * @return {[type]} [description]
       */
      backToMapping() {
        this.showType = 'mapping';
      },
      /**
       * excelgeojson
       * @param  {[Array]} a      [excel]
       * @param  {[Object]} fields []
       * @return {[Object]}        [GeoJSON]
       */
       getGeoJSON: function( a, fields ) {
        var geojson = {
          "type": "FeatureCollection",
          "features": null,
         };

        geojson.features = a.map( function( value, index ) {
          var feature = {
            "type": "Feature",
            "geometry": {
              "type": "Point",
              "coordinates": [ value[ fields.x ], value[ fields.y ] ],
            },
            "properties": {

            }
          }
          return feature;
        } )

        return geojson;
      },
      /**
       * 
       * @param string type 
       * @return {[type]} [description]
       */
      editMapElements( type ) {
        if( type === 'print' || type === 'mapping' ) {
          this.showType = type;
          return;
        }
        debugger
        this.editable  = type;
        if( this.editable !== 'feature' ) {
          this.drawType = null;
          this.__global__.olMap.removeInteraction( this.painting );
        }
      },
      /**
       * 
       * @param  {string} type 
       * @return {[type]}      [description]
       */
      draw:function( type ) {

          var map = this.__global__.olMap;

          if( !added ) { map.addLayer( vector ); added = true; }

          // canvasolcanvas
          if( type === this.drawType || this.editable !== 'feature' ) {
            this.drawType = null;
            map.removeInteraction( this.painting );
            return;
          }

          this.drawType = type;

          this.painting = new ol.interaction.Draw({
            source: source,
            type: 'LineString'
          })

          map.addInteraction( this.painting );
      },
      /**
       * (),label
       * @param Array featureArr 
       * @param number index params
       * @param String textField 
       * @return {[type]} [description]
       */
      passFeatures( index, featureArr,textField,showField ) {
        debugger
        this.features[ index ] = featureArr.map( ( feature, i ) => {
          var a = {};

          // origin
          switch ( this.params[ index ].layer ) {
            case 'PointLayer':
              // debugger;
              a.origin = feature.getGeometry().getCoordinates();
              a.name = feature.values_[textField];
              if(showField){
                a.value = feature.values_[showField];
              }
              // a.text = this.params[ index ].text;//
              break;
            case 'LineLayer':
              // a.origin =
              break;
            case 'PolygonLayer':

              break;
          }
          return a;
        } )
      },
    },
    created() {
      /* ,, */
      // 
      // 
      this.$Spin.show();
      //
      if(this.__global__.mappingTemplate === null){
        // 
        const MAPINGTYPE = this.__global__.type;
        // 
        const DATA = this.__global__.mappingData;
        // 
        var params = [];

        DATA.forEach( ( value, index ) => {
          // 
          const DATATYPE = value.geotype;
          // 
          const Param = mappingParams[ DATATYPE ] || mappingParams[ "esriGeometryPoint" ];
          var p = new Param();

          // 
          if( value.type === 'default' ) {
            debugger;
            // ()
            if(DATATYPE === "esriGeometryPolygon"){
              p.text = value.rules.text;
              p.color = value.rules.color;
              p.stylePic = value.rules.stylePic;
              //-->,observer()
              p.font = value.rules.text.font;//
              p.fontStyle = value.rules.text.fontStyle;//
              p.fontSize = value.rules.text.fontSize;//
              p.fontFamily = value.rules.text.fontFamily;//
              p.fontFill = value.rules.text.fill.color_,//
              p.fontStrokeColor = value.rules.text.stroke.color_,//
              p.fontStrokeWidth = value.rules.text.stroke.width_;//
              p.offsetX = value.rules.text.offsetX;//x
              p.offsetY = value.rules.text.offsetY;//y
              p.textAlign = value.rules.text.textAlign;//
            }
            if(DATATYPE === "esriGeometryPolyline"){
              p.text = value.rules.text;
              p.color = value.rules.color;
              p.stylePic = value.rules.stylePic;
              p.custom = value.rules.custom;
              //-->,observer()
              p.font = value.rules.text.font;//
              p.fontStyle = value.rules.text.fontStyle;//
              p.fontSize = value.rules.text.fontSize;//
              p.fontFamily = value.rules.text.fontFamily;//
              p.fontFill = value.rules.text.fill.color_,//
              p.fontStrokeColor = value.rules.text.stroke.color_,//
              p.fontStrokeWidth = value.rules.text.stroke.width_;//
              p.offsetX = value.rules.text.offsetX;//x
              p.offsetY = value.rules.text.offsetY;//y
              p.textBaseline = value.rules.text.textBaseline;//
              p.maxAngle = value.rules.text.maxAngle;
            }
            if(DATATYPE === "esriGeometryPoint"){
              if(value.name ===""){
                if(this.__global__.type === 'template3'){
                  p.text = value.rules[1].text;
                  p.color = value.rules[1].color;
                  p.stylePic = p.src = value.rules[1].stylePic;
                  p.showLabels = value.rules[1].showLabels;
                  p.scale = value.rules[1].scale;
                  //-->,observer()
                  p.font = value.rules[1].text.font;//
                  p.fontStyle = value.rules[1].text.fontStyle;//
                  p.fontSize = value.rules[1].text.fontSize;//
                  p.fontFamily = value.rules[1].text.fontFamily;//
                  p.fontFill = value.rules[1].text.fill,//
                  p.fontStrokeColor = value.rules[1].text.stroke,//
                  p.fontStrokeWidth = value.rules[1].text.strokeWidth;//
                }else{
                  p.text = value.rules[0].text;
                  p.color = value.rules[0].color;
                  p.stylePic = p.src = value.rules[0].stylePic;
                  p.showLabels = value.rules[0].showLabels;
                  p.scale = value.rules[0].scale;
                  //-->,observer()
                  p.font = value.rules[0].text.font;//
                  p.fontStyle = value.rules[0].text.fontStyle;//
                  p.fontSize = value.rules[0].text.fontSize;//
                  p.fontFamily = value.rules[0].text.fontFamily;//
                  p.fontFill = value.rules[0].text.fill,//
                  p.fontStrokeColor = value.rules[0].text.stroke,//
                  p.fontStrokeWidth = value.rules[0].text.strokeWidth;//
                }
              }
              else{
                p.text = value.rules.text;
                p.color = value.rules.color;
                p.stylePic = p.src = value.rules.stylePic;
                p.showLabels = value.rules.showLabels;
                p.scale = value.rules.scale;
                //-->,observer()
                p.font = value.rules.text.font;//
                p.fontStyle = value.rules.text.fontStyle;//
                p.fontSize = value.rules.text.fontSize;//
                p.fontFamily = value.rules.text.fontFamily;//
                p.fontFill = value.rules.text.fill,//
                p.fontStrokeColor = value.rules.text.stroke,//
                p.fontStrokeWidth = value.rules.text.strokeWidth;//
              }
            }

            // 
            if(value.hasOwnProperty('showNodes')){
              debugger
              if(value.name ===""){
                if(this.__global__.type === 'template3'){
                  p.zoomArray = value.showNodes.zoomArray[1];
                  p.showField = value.showNodes.showField;
                  p.showFieldValue = value.showNodes.zoomArray[1][1];//(zoom=8)
                }else{
                  p.zoomArray = value.showNodes.zoomArray[0];
                  p.showField = value.showNodes.showField;
                  p.showFieldValue = value.showNodes.zoomArray[0][1];//(zoom=8)
                }
              }else{
                p.zoomArray = value.showNodes.zoomArray;
                p.showField = value.showNodes.showField;
                p.showFieldValue = value.showNodes.zoomArray[1];//(zoom=8)
              }
            }

            // observable
            // where(where=ADCD='')
            var filter;
            if(this.__global__.type == 'template3'){
              // filter = "ADCD=''";//
              //   if(value.name ===""){
              //     filter = "ADCD='' or ADMN=''";
              //   }
              filter = "1=1";//()
            }
            else{
              filter = "1=1";//()
            }
           
            p.url = `${value.address}/0/query/?f=json&returnGeometry=true&spatialRel=esriSpatialRelIntersects&geometry=&geometryType=${value.geotype}&outFields=*&where=${filter}`;
            p.textField = value.text;
            p.gradeField= value.gradeField;
          }

          // 
          else if( value.type === 'custom' ) {
            // 
            p.url = this.getGeoJSON( value.data, value.data.fields )
          }
          debugger
          // 
          observe( p );

          p.container = params;//p.containerlayers -->param.container.doneRequestParams++;
          // 
          p.name = value.name;
          params.push( p );

        })
        this.params = params;
        // 
        params.doneRequestParams = 0;
        // 
        const allDone = () => {
          this.$Spin.hide();
          this.params.forEach( ( value ) => { delete value.container } );
        };
        params.callback( allDone );
      }

      // 
      else{
        let templateContent = this.__global__.mappingTemplate;
        // 
        let params = [];
        templateContent.forEach( ( value, index ) => {
          // 
          let DATATYPE = "";
          let layerType = value.layer;
          switch(layerType){
            case "LineLayer":
              DATATYPE = "esriGeometryPolyline"
              break;
            case "PointLayer":
              DATATYPE = "esriGeometryPoint"
              break;
            case "PolygonLayer":
              DATATYPE = "esriGeometryPolygon"
              break;
            default: break;
          }
          // 
          const Param = mappingParams[ DATATYPE ];
          var p = new Param();

          // 
          debugger
          p = value;
          // 
          if(layerType=='PolygonLayer'){
            if(typeof(value.color)=== "object"){
              // canvas
              var cnv = document.createElement('canvas');
              var ctx = cnv.getContext('2d');
              var pattern, img;

              /**
               * 
               * @param  {[type]} src [description]
               * @return {[type]}     [description]
               */
              function getImage( src ){
                return new Promise( ( resolve, reject ) => {
                  var img = new Image();
                  img.src = src;
                  img.onload = function () {
                    resolve( this );
                  }
                } )
              }
              //thenresolvedrejected
              //thenPromise,thenthen
              getImage(value.stylePic)
              .catch((error)=> {
                console.log('oh no', error);
              })
              .then((img)=>{ 
                pattern = ctx.createPattern(img, 'repeat');
                p.color = pattern;
              });
            }else{
              p.color = value.color;
            }
          }

          // 
          observe( p );

          // // 
          // p.name = value.name;
          // p.layer = value.layer;
          // p.url = value.url;
          // // p.showLabels = value.showLabels;   //
          // p.stylePic = value.stylePic;
          // p.visible = value.visible;
          // // 
          // p.textField = value.textField;
          // p.text = value.text;
          // p.fontStyle = value.fontStyle;
          // p.fontSize = value.fontSize;
          // p.font = value.font;
          // p.fontFamily = value.fontFamily;
          // p.fontFill = value.fontFill;
          // p.fontStrokeColor = value.fontStrokeColor;
          // p.fontStrokeWidth = value.fontStrokeWidth;
          // // p.color = value.color;

          // switch(layerType){
          //   case "LineLayer":
          //     p.color = value.color;
          //     p.checksum = value.checksum;
          //     p.interval = value.interval;
          //     p.lineCap = value.lineCap;
          //     p.lineDash = value.lineDash;
          //     p.lineDashOffset = value.lineDashOffset;
          //     p.lineJoin = value.lineJoin;
          //     p.miterLimit = value.miterLimit;
          //     p.width = value.width;
          //     p.custom = value.custom;
          //     p.offsetX = value.offsetX;//x
          //     p.offsetY = value.offsetY;//y
          //     p.textBaseline = value.textBaseline;//
          //     p.maxAngle = value.maxAngle;
          //     p.manual_width = value.manual_width; //
          //     p.manual_interval = value.manual_interval;//
          //     break;
          //   case "PointLayer":
          //     p.color = value.color;
          //     p.crossOrigin = value.crossOrigin;
          //     p.src = value.src;
          //     p.opacity = value.opacity;
          //     p.scale = value.scale;
          //     p.isGrade = value.isGrade;
          //     p.gradeCount = value.gradeCount;
          //     p.gradeField = value.gradeField;
          //     p.gradeMethod = value.gradeMethod;
          //     p.stopColor = value.stopColor;
          //     p.startColor = value.startColor;
          //     p.isChart = value.isChart;
          //     p.chartID = value.chartID;
          //     p.statFields = value.statFields;
          //     p.colors = value.colors;
          //     p.isCluster = value.isCluster;
          //     p.distance = value.distance;
          //     p.clusterColor = value.clusterColor;
          //     break;
          //   case "PolygonLayer":
          //     if(typeof(value.color)=== "object"){
          //       // canvas
          //       var cnv = document.createElement('canvas');
          //       var ctx = cnv.getContext('2d');
          //       var pattern, img;

          //       /**
          //        * 
          //        * @param  {[type]} src [description]
          //        * @return {[type]}     [description]
          //        */
          //       function getImage( src ){
          //         return new Promise( ( resolve, reject ) => {
          //           var img = new Image();
          //           img.src = src;
          //           img.onload = function () {
          //             resolve( this );
          //           }
          //         } )
          //       }
          //       //thenresolvedrejected
          //       //thenPromise,thenthen
          //       getImage(value.stylePic)
          //       .catch((error)=> {
          //         console.log('oh no', error);
          //       })
          //       .then((img)=>{ 
          //         pattern = ctx.createPattern(img, 'repeat');
          //         p.color = pattern;
          //       });
          //     }else{
          //       p.color = value.color;
          //     }
          //     p.isGrade = value.isGrade;
          //     p.gradeCount = value.gradeCount;
          //     p.gradeField = value.gradeField;
          //     p.gradeMethod = value.gradeMethod;
          //     p.stopColor = value.stopColor;
          //     p.startColor = value.startColor;
          //     p.stylePic = value.stylePic;
          //     p.offsetX = value.offsetX;//x
          //     p.offsetY = value.offsetY;//y
          //     p.textAlign = value.textAlign;//
          //     break;
          //   default: break;
          // }

          p.container = params;
          params.push( p );

        })

        this.params = params;
        // 
        params.doneRequestParams = 0;
        // 
        const allDone = () => {
          this.$Spin.hide();
          this.params.forEach( ( value ) => { delete value.container } );
        };
        params.callback( allDone );
      }

    },
  }

</script>

