<script>
export default {
  name: "App",
  data() {
    return {
      activeName: "home"
    };
  },
  created() {
    // active menuitem
    this.activeName = this.$route.path.slice(1);
    // 
    var UserAgent = navigator.userAgent.toLowerCase();
    var browser = null;
    var browserArray = {
      IE: window.ActiveXObject || "ActiveXObject" in window, // IE
      Chrome: UserAgent.indexOf("chrome") > -1 && UserAgent.indexOf("safari") > -1, // Chrome
      Firefox: UserAgent.indexOf("firefox") > -1, // 
      Opera: UserAgent.indexOf("opera") > -1, // Opera
      Safari: UserAgent.indexOf("safari") > -1 && UserAgent.indexOf("chrome") == -1, // safari
      Edge: UserAgent.indexOf("edge") > -1, // Edge
      QQBrowser: /qqbrowser/.test(UserAgent), // qq
      WeixinBrowser: /MicroMessenger/i.test(UserAgent) // 
    };
    for (var i in browserArray) {
      if (browserArray[i]) {
        browser = i;
      }
    }
    this.__global__.browserType = browser;
    // console.log(browser);

    //
    if (this.__global__.href == null) {
      this.__global__.href = window.location.href;
    }
    // console.log(this.__global__.href);

    // session
    this.__global__.templateUser = sessionStorage.getItem("username");//()
    // this.__global__.userName = sessionStorage.getItem("fullname");//sessionStorage.getItem("fullname")
    // this.__global__.templateUser = "slpt1";//

    if(this.__global__.templateUser != null){
      // this.__global__.allData
      let obj = {
        username: this.__global__.templateUser
      };
      var options = {
        method: 'POST',
        headers: {
          "Content-type": "application/json"
        },
        body:JSON.stringify(obj)
      };
      fetch('http://118.178.119.202:8080/waterServer/service/queryAccessServer',options)
      .then(response=>response.json())
        .then(json=>{
          if(json.pageData.length > 0){
            // 
            var filterData = new Set();//
            this.__global__.allData.map( function( data, i ) {
              json.pageData.forEach(( value, index ) =>{
                // console.log(value.title);
                if(value.title == data.name || data.name.indexOf(value.title)>-1){
                  filterData.add(data);
                }
              })
            });
            this.__global__.allData = [...filterData];
          }else{
            alert("");
            this.__global__.allData = [];
          }
      });
    }else{
      alert("");
      this.__global__.allData = [];
    }
    
  },
  watch: {
    $route() {
      //
      // this.$refs.mapmenu.currentActiveName = this.$route.path.slice(1) || 'home';
    }
  },
  methods: {
    // togodata () {
    //   switch(this.$route.path.slice(1)) {
    //     case '':
    //     console.log(this.$refs.mapmenu.currentActiveName);
    //     this.$refs.mapmenu.currentActiveName = 'home';
    //     // console.log(this.$refs.mapmenu.currentActiveName);
    //     alert("");
    //     // console.log(this.$refs.mapmenu.currentActiveName);
    //     break;
    //     case 'map':
    //     this.$router.go(-1);
    //     break;
    //   }
    // },
    // togomap () {
    //   switch(this.$route.path.slice(1)) {
    //     case '':
    //     this.$refs.mapmenu.currentActiveName = 'home';
    //     alert("");
    //     break;
    //     case 'data':
    //     if (this.__global__.mappingData.length > 0) {
    //       this.$router.push({path: '/map'});
    //     }else {
    //       this.$refs.mapmenu.currentActiveName = 'home';
    //       alert("");
    //     }
    //     break;
    //   }
    // },
    // togohome () {
    //   this.__global__.mappingData = [];
    //   this.$router.push({path: '/'});
    // },
  }
};
</script>


<style >
/* app.vue */
.q {
