function PolylineParam(type) {

  PublicProp.call( this, 'LinePanel', 'LineLayer' );

  /**
   *
   * @type {ol.Color|ol.ColorLike}
   */
  this.color = 'rgba(139, 0, 0, 1)';

  /**
   *
   * @type {string|undefined}
   */
  this.lineCap = 'round';

  /**
   *
   * @type {Array.<number>}
   */
  this.lineDash = null;

  /**
   *
   * @type {number|undefined}
   */
  this.lineDashOffset = null;

  /**
   *
   * @type {string|undefined}
   */
  this.lineJoin = 'round';

  /**
   *
   * @type {number|undefined}
   */
  this.miterLimit = null;

  /**
   *
   * @type {number|undefined}
   */
  this.width = 0.2;

  /**
   *
   * @type {string|undefined}
   */
  this.checksum = undefined;

  /**
   * 
   * @type {[type]}
   */
  this.custom = null;

  this.interval = 1;

  this.manual_width = false;//
  this.manual_interval = false;//

  /**
   * 
   * @type {String}
   */
  this.stylePic = './static/LineLayer/template1/.png';

  // 
  this.fontStyle = "normal";
  this.fontSize = 14;
  this.font = "";
  this.fontFamily = "sans-serif";
  this.fontFill = "#003366";
  this.fontStrokeColor = "#ffffff";
  this.fontStrokeWidth = 3;
  this.offsetX = 0;
  this.offsetY = 0;
  this.textBaseline = "middle";
  this.maxAngle = Math.PI / 4,
  this.text = {
    // font: '13px ,sans-serif',
    // scale: 1,//
    // placement:'line',
    // rotateWithView:true,
    // maxAngle:Math.PI / 2,
    // // overflow:true,//
    // fill: new ol.style.Fill({
    //   color: '#000'
    // }),
    // stroke: new ol.style.Stroke({
    //   color: '#fff',
    //   width: 3
    // })
  };

};

function PointParam( type ) {

  PublicProp.call( this, 'PointPanel', 'PointLayer' );

  this.color = '#576C84';
  this.crossOrigin = 'anonymous';
  // this.src = 'https://openlayers.org/en/v4.6.4/examples/data/dot.png';
  this.src = this.stylePic = './static/2.png';
  this.opacity = 1;
  // 
  this.scale = 0.3;

  // ------------
  this.isGrade = false;
  // 
  this.gradeCount = 3;
  // 
  this.gradeField = null;
  // 
  this.gradeMethod = '';
  // 
  this.stopColor = '#99490f';
  // 
  this.startColor = '#fee9da';


  // -----------------
  this.isChart = false;
  // id
  this.chartID = '010101';
  // 
  this.statFields = [];
  // 
  this.colors = [ '#ffc12a', '#ffff3a', '#90cf5b', '#00af57', '#00afee', '#0071be', '#00215f', '#72349d', '#ffc12a', '#ffff3a', '#90cf5b', '#00af57', '#00afee', '#0071be', '#00215f', '#72349d' ];

  // --------------
  this.isCluster = false;
  // 
  // 20
  this.distance = 30;
  // 
  this.clusterColor = '#3399CC';

  // 
  this.gradeCount = 3;
  // 
  this.gradeField = null;
  // 
  this.gradeMethod = '';

  // 
  this.fontStyle = "normal";
  this.fontSize = 10;
  this.font = "";
  this.fontFamily = "sans-serif";
  this.fontFill = "#003366";
  this.fontStrokeColor = "#ffffff";
  this.fontStrokeWidth = 3;
  this.textManual = false;//
  // 
  this.text = {
    // font: '14px ,sans-serif',
    // scale: 1,//
    // // overflow:true,//
    // fill: new ol.style.Fill({
    //   color: '#000'
    // }),
    // stroke: new ol.style.Stroke({
    //   color: '#fff',
    //   width: 3
    // })
  };

  // 
  this.zoomArray = null;
  this.showField = null;
  this.showFieldValue = null;

}

function PolygonParam( type ) {
  /* function.call() || function.apply() */
  // this
  // 
  // 1.applythis[agruments]
  // 2.call()apply()
  PublicProp.call( this, 'PolygonPanel', 'PolygonLayer' );
  this.color = '#4b5cc4';
  // 
  this.isGrade = false;
  // 
  this.gradeCount = 3;
  // 
  this.gradeField = null;
  // 
  this.gradeMethod = '';
  // 
  this.stopColor = '#99490f';
  // 
  this.startColor = '#fee9da';

  this.stylePic = this.color;

  // 
  this.fontStyle = "normal";
  this.fontSize = 14;
  this.font = "";
  this.fontFamily = "sans-serif";
  this.fontFill = "#003366";
  this.fontStrokeColor = "#ffffff";
  this.fontStrokeWidth = 3;
  this.offsetX = 0;
  this.offsetY = 0;
  this.textAlign = "center";
  this.text = {
    // font: '14px ,sans-serif',
    // fill: new ol.style.Fill({
    //   color: '#000080'
    // }),
    // stroke: new ol.style.Stroke({
    //   color: '#ffffff',
    //   width: 3
    // }),
    // scale: 1,//
    // placement:'point',
    // overflow: true,//

    //
    // backgroundStroke:new ol.style.Stroke({
    //     color:'#003366',
    //     width:1
    // }),
    // //
    // backgroundFill:new ol.style.Fill({
    //     color:'#003366'
    // })
  };
  // 
  this.zoomArray = null;
  this.showField = null;
  this.showFieldValue = null;
}

/**
 * 
 * @param       {[type]} panel [description]
 * @param       {[type]} layer [description]
 * @constructor
 */
function PublicProp( panel, layer ) {
  // 
  this.visible = true;
  // 
  this.showLabels = false;
  // // 
  // this.url = null;
  // 
  this.layer = layer;
  // 
  this.panel = panel;
  // 
  this.stylePic = null;
  // 
  this.textField = null;
  // 
  this.isText = false;
  // 
  this.zoom = null;
  // 
  this.center = null;
}



export {
  PolylineParam,
  PointParam,
  PolygonParam,
}



