  }
  .formDiv{
    position: absolute;
    top: calc(50vh - 180px);
    right: calc(50vw - 200px);
    width: 370px;
    height: 360px;
    display: flex;
    border:1px solid #BFBFBF;
    box-shadow:5px 5px 10px 5px #aaa;
  }
  .formTitle{
  	text-align: center;
  	margin-top: 30px;
  	margin-bottom: 30px;
  	font-size: 20px;
  	font-weight: bold;
  }

  .printDiv{
    position: absolute;
    top: calc(50vh - 180px);
    right: calc(50vw - 200px);
    width: 370px;
    height: 400px;
    display: flex;
    border:1px solid #BFBFBF;
    box-shadow:5px 5px 10px 5px #aaa;
  }

  .ivu-select{
    width: 240px;
  }

  .ivu-input-wrapper{
    width: 260px;
  }
   /*.fontsize.ivu-form-item

 .ivu-form-item-label{
    font-size: 20px;
   }            */
</style>



